.usuario p:not(.mensaje__error) {
  margin-top: 0;
}
.usuario b {
  font-weight: 600;
  text-transform: uppercase;
}
.usuario__contenedor {
  display: grid;
  grid-template-columns: 220px 1fr;
  grid-template-rows: auto auto auto;
  column-gap: 100px;
}
@media (max-width: 1024px) {
  .usuario__contenedor {
    column-gap: 25px;
  }
}
.usuario__navegacion {
  width: 220px;
}
.usuario__navegacion li {
  height: 50px;
}
.usuario__enlace {
  align-content: center;
  height: 100%;
  width: 90%;
  display: inline-block;
  font-size: 20px;
  color: #4E342E;
  padding-left: 1rem;
  border-radius: 6px;
}
.usuario__enlace.ruta_actual {
  background-color: rgb(110.0806451613, 73.3870967742, 64.9193548387);
  color: #F5E6D3;
}
.usuario__enlace:hover {
  background-color: rgb(126.1209677419, 84.0806451613, 74.3790322581);
  color: #F5E6D3;
  cursor: pointer;
  font-weight: 800;
}
.usuario .datos__contenedor {
  display: grid;
  grid-template-columns: 600px 200px;
}
@media (max-width: 1200px) {
  .usuario .datos__contenedor {
    grid-template-columns: 640px;
  }
}
.usuario__datos {
  display: grid;
  grid-template-columns: 300px 200px;
  height: 150px;
}
@media (max-width: 768px) {
  .usuario__datos {
    grid-template-columns: 350px;
    height: fit-content;
  }
}
.usuario__acciones {
  margin-bottom: 3rem;
}
@media (max-width: 1200px) {
  .usuario__acciones {
    display: flex;
    gap: 20px;
  }
}
@media (max-width: 1024px) {
  .usuario__acciones {
    flex-direction: column;
    gap: 0;
  }
}
.usuario__accion {
  width: 200px;
  margin-bottom: 10px;
}
.usuario .formulario {
  grid-row: 4;
}
.usuario .formulario__contenedor {
  display: flex;
}
.usuario .alerta {
  grid-row: 3;
}
@media (max-width: 480px) {
  .usuario {
    padding: 0;
  }
}

.usuario__lista-direcciones {
  width: 350px;
  margin-top: 20px;
}
.usuario__direccion {
  border: 1px solid #4E342E;
  padding: 10px 15px 15px 15px;
  background-color: #FAF3E0;
  border-radius: 5px;
  position: relative;
}
.usuario__direccion h3, .usuario__direccion p {
  display: inline;
}
.usuario__direccion-predeterminada {
  position: absolute;
  right: 15px;
}
.usuario__direccion-datos {
  margin-top: 10px;
  margin-bottom: 10px;
}
.usuario__direccion-acciones {
  display: flex;
  gap: 5px;
}