.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 8rem;
  padding-left: 5.5%;
  padding-right: 5.5%;
  background: #4E342E;
  background-image: url("../img/onda.svg");
  background-repeat: repeat-x;
  background-size: 150px 80px;
  background-position: bottom;
  color: #F5E6D3;
  padding-bottom: 40px;
}
.header__logo {
  font-size: 3rem;
  color: #F5E6D3;
  font-weight: 700;
}
.header__logo:hover {
  font-size: 3.1rem;
  color: #D4AF37;
}
.header__navegacion {
  width: 50%;
}
.header__navegacion ul {
  display: flex;
  justify-content: end;
  list-style: none;
  gap: 2rem;
  padding: 0;
  width: 100%;
}
.header__enlace, .header__submit {
  color: #F5E6D3;
  font-size: 1.2rem;
  font-weight: 600;
}
.header__enlace:hover, .header__submit:hover {
  cursor: pointer;
  color: #D4AF37;
}
.header__submit {
  background-color: transparent;
  border: none;
}
.header__admin {
  display: flex;
}
.header__admin .admin__fecha {
  display: flex;
}

.footer {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr auto;
  justify-content: center;
  background-color: #4E342E;
  padding-top: 30px;
  padding-left: 5.5%;
  padding-right: 5.5%;
  margin-top: 70px;
  color: #F5E6D3;
}
.footer__contacto {
  grid-column: 1/3;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  row-gap: 20px;
}
.footer__campo {
  display: flex;
  gap: 10px;
}
.footer__contenedor-redes {
  text-align: end;
}
.footer__contenedor-redes p {
  margin-top: 0;
}
.footer__redes a {
  color: #F5E6D3;
}
.footer__redes a:hover {
  color: #D4AF37;
}
.footer__copyright {
  grid-row: 2;
  grid-column: 1/4;
  text-align: center;
}

.login,
.registro {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.formulario {
  min-width: 350px;
  width: 30%;
  margin-bottom: 20px;
}
.formulario__contenedor {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 100px;
}
@media (max-width: 1200px) {
  .formulario__contenedor {
    grid-template-columns: 1fr;
    max-width: 500px;
    width: 80%;
  }
}
.formulario__legend {
  font-size: 1.25rem;
  margin-bottom: 15px;
  font-weight: 600;
}
.formulario__campo {
  display: flex;
  flex-direction: column;
}
.formulario__label {
  margin-bottom: 10px;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 0.5px;
  font-size: 1.15rem;
  display: flex;
  align-items: center;
  gap: 10px;
}
.formulario__label--input {
  width: fit-content;
  margin-bottom: 0;
}
.formulario__label--input:hover {
  cursor: pointer;
}
.formulario__label--recuerdame {
  font-size: 15px;
  margin-top: -30px;
}
.formulario__input {
  border-radius: 0.25rem;
  height: 3rem;
  border: 1px solid #4E342E;
  margin-bottom: 2rem;
  padding-left: 1rem;
  font-size: 1.2rem;
  color: #4E342E;
}
.formulario__input::placeholder {
  color: rgb(178.0241935484, 134.5161290323, 124.4758064516);
  font-size: 1.2rem;
}
.formulario__input--file {
  padding-left: 0;
  width: 100%;
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 32px;
}
.formulario__input--img {
  display: flex;
  flex-direction: column;
}
.formulario__input-button {
  padding-left: 1rem;
  padding-right: 1rem;
  line-height: 3rem;
  width: 105px;
  flex: 0 0 auto;
  height: 100%;
  white-space: nowrap;
  border-right: 1px solid hsl(7, 20%, 60%);
}
.formulario__input-button:hover {
  cursor: pointer;
  font-weight: 600;
}
.formulario__input-text {
  flex: 1 1 auto;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.formulario__input--radio {
  accent-color: #4E342E;
  margin-bottom: 0;
}
.formulario__input--radio:hover {
  cursor: pointer;
}
.formulario__input--select:hover {
  cursor: pointer;
}
.formulario__input--error:not(.campo-entrega) {
  border: 1px solid #D9534F;
  color: #D9534F;
  margin-bottom: 0;
}
.formulario__input--error:not(.campo-entrega)::placeholder {
  color: rgb(239.6401869159, 185.476635514, 183.8598130841);
}
.formulario__option:hover {
  background-color: #F5E6D3;
}
.formulario__submit {
  height: 2.5rem;
  width: 10rem;
  text-transform: uppercase;
  color: #F5E6D3;
  font-weight: 600;
  background-color: #4E342E;
  border-radius: 50px;
  border: none;
}
.formulario__submit:hover {
  cursor: pointer;
  background-color: #F5E6D3;
  color: #4E342E;
  outline: 2px #4E342E solid;
}
.formulario__img {
  width: 200px;
  height: auto;
  border-radius: 15px;
}
.formulario--admin {
  width: 50%;
  max-width: 750px;
}
.formulario--admin .formulario__label {
  color: #1f2933;
}
.formulario--admin .formulario__input {
  border: 1px solid #dce3ec;
  color: #333333;
}
.formulario--admin .formulario__input::placeholder {
  color: rgb(146.8808510638, 152.7936170213, 164.6191489362);
  font-size: 1.2rem;
}
.formulario--admin .formulario__input-button {
  border-right: 1px solid #dce3ec;
  background-color: #dce3ec;
}
.formulario--admin .formulario__input-button:hover {
  background-color: rgb(192.6136363636, 209.25, 225.8863636364);
}
.formulario--modal {
  width: fit-content;
  border-radius: 15px;
}
@media (max-width: 768px) {
  .formulario--modal .formulario__contenedor {
    grid-template-columns: 1fr;
  }
}
.formulario--modal .campo-pais {
  width: 256px;
}
@media (max-width: 768px) {
  .formulario--modal .campo-pais {
    width: 100%;
  }
}
.formulario .boton {
  margin-bottom: 20px;
}
.formulario__acciones {
  display: flex;
  justify-content: space-between;
}
.formulario__acciones a {
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
}

.acciones {
  display: flex;
  min-width: 325px;
  width: 30%;
  justify-content: space-between;
  gap: 5px;
}
.acciones input {
  border: none;
  background-color: transparent;
}
.acciones input:hover {
  cursor: pointer;
}
.acciones--centrar {
  justify-content: center;
}
.acciones__enlace {
  color: #4E342E;
}
.acciones__enlace:hover {
  color: #D4AF37;
}
.acciones__enlace--reenviar {
  margin-bottom: 10px;
  margin-top: -10px;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 1000px #fff inset !important; /* tu color de fondo */
  -webkit-text-fill-color: #4E342E !important; /* color del texto */
  transition: background-color 5000s ease-in-out 0s; /* truco para evitar el parpadeo */
}

.alerta {
  box-sizing: border-box;
  min-width: 325px;
  width: 30%;
  margin-bottom: 20px;
  padding: 10px 1rem;
  color: white;
  font-weight: 600;
  font-size: 17px;
  text-transform: uppercase;
  text-align: center;
  border-radius: 7px;
}
.alerta__error {
  background-color: #b71c1c;
}
.alerta__exito {
  background-color: rgb(1.5384615385, 168.4615384615, 1.5384615385);
}

.mensaje__error {
  color: #D9534F;
  margin-top: 5px;
  margin-bottom: 5px;
  font-weight: 600;
  margin-bottom: 15px;
}
.mensaje__error--general {
  margin-top: -15px;
}

.tabla {
  border-collapse: collapse;
  margin-bottom: 50px;
}
.tabla__header, .tabla a {
  color: #333333;
}
.tabla__body .tabla__fila:nth-child(odd) {
  background-color: #4E342E;
  color: #F5E6D3;
}
.tabla__body .tabla__fila:nth-child(odd) .boton {
  background-color: #F5E6D3;
  color: #4E342E;
}
.tabla__body .tabla__fila:nth-child(odd) .boton:hover {
  background-color: #4E342E;
  color: #F5E6D3;
  outline: 2px solid #F5E6D3;
}
.tabla__body .tabla__fila .boton {
  border: none;
}
.tabla__fila {
  height: 45px;
}
.tabla__celda {
  text-align: center;
  padding-left: 15px;
  padding-right: 15px;
}
.tabla__celda--no-wrap {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.tabla__celda img {
  height: 100px;
  vertical-align: middle;
  border-radius: 8px;
  margin-top: 5px;
  margin-bottom: 5px;
}
.tabla--admin .tabla__fila:hover:not(.tabla__fila--no-hover) {
  cursor: pointer;
}
.tabla--admin .tabla__celda {
  font-size: 25px;
}
.tabla--admin .tabla__celda--head:hover:not(.tabla__celda--no-hover) {
  background-color: rgb(192.6136363636, 209.25, 225.8863636364);
}
.tabla--admin .tabla__body .tabla__fila:nth-child(odd) {
  color: #6b7280;
  background-color: #dce3ec;
}
.tabla--admin .tabla__body .tabla__fila:hover:not(.tabla__fila--no-hover) {
  background-color: rgb(192.6136363636, 209.25, 225.8863636364);
  transition: background-color 0.5s ease;
}

.paginacion {
  display: flex;
  justify-content: center;
  font-size: 20px;
  font-weight: 600;
}
.paginacion a, .paginacion span:not(.paginacion__boton) {
  border-radius: 5px;
  padding: 3px 10px 3px 10px;
}
.paginacion__boton {
  margin-right: 5px;
  padding: 0;
  width: 35px;
  height: 35px;
  font-size: 20px;
  font-weight: 600;
  border: 1px solid #4E342E;
  background-color: transparent;
  border-radius: 5px;
}
.paginacion__boton:hover:not(.paginacion__pagina-actual) {
  cursor: pointer;
  color: #F5E6D3;
  background-color: #4E342E;
}
.paginacion__pagina-actual {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #D4AF37;
  background-color: #4E342E;
}
.paginacion--admin a {
  color: rgb(79.2012195122, 104.75, 130.2987804878);
}
.paginacion--admin a:hover {
  background-color: #D2DEEA;
}

.carrito {
  background-color: hsl(20, 50%, 98%);
  width: 385px;
  height: fit-content;
  border-radius: 10px;
}
.carrito--modal {
  width: 480px;
  max-height: 100%;
  overflow: auto;
}
.carrito__resumen {
  text-align: center;
  font-size: 22.5px;
  padding-top: 25px;
  padding-bottom: 15px;
  margin-bottom: 20px;
}
.carrito__contador {
  margin-bottom: 30px;
  margin-top: 30px;
  margin-left: 25px;
}
.carrito__numero-fecha {
  width: 100%;
  display: flex;
  justify-content: space-between;
  font-size: 20px;
  margin-bottom: 10px;
}
.carrito__direccion {
  font-size: 20px;
  width: 100%;
  margin-bottom: 10px;
}
.carrito__direccion p {
  margin-top: 0;
}
.carrito__direccion-datos {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.carrito__contenido, .carrito .carrito__vacio {
  display: grid;
  place-items: center;
}
.carrito__contenido {
  margin-left: 6.5%;
  margin-right: 6.5%;
  width: 87%;
}
.carrito__lista {
  width: 100%;
}
.carrito__articulo {
  border-bottom: 1px solid lightgray;
  align-items: center;
  margin-bottom: 20px;
}
.carrito__articulo, .carrito .carrito__precio-cantidad, .carrito .carrito__total {
  display: flex;
}
.carrito__articulo, .carrito .carrito__total {
  justify-content: space-between;
}
.carrito__imagen {
  width: 100px;
  height: 100px;
  margin-bottom: 10px;
}
.carrito__datos--modal {
  width: 200px;
}
.carrito__nombre {
  margin-top: 0;
}
.carrito__precio-cantidad {
  margin-top: 15px;
  font-weight: 600;
}
.carrito__precio-unitario {
  margin-right: 5px;
  margin-left: 5px;
  font-weight: 400;
  margin-bottom: 10px;
}
.carrito__boton-eliminar {
  background-color: #4E342E;
  color: #F5E6D3;
  width: 20px;
  height: 20px;
  border-radius: 50px;
  display: grid;
  place-content: center;
  border: none;
}
.carrito__boton-eliminar:hover {
  cursor: pointer;
  color: #4E342E;
  background-color: #F5E6D3;
  outline: 1px solid #4E342E;
}
.carrito__total {
  width: 100%;
  align-items: center;
  font-weight: 700;
  font-size: 25px;
}
.carrito__total-precio {
  font-weight: 600;
}
.carrito__total-articulos {
  margin-top: 0;
}
.carrito__boton-confirmar {
  margin-top: 25px;
  margin-bottom: 25px;
  width: 80%;
  background-color: #4E342E;
  border: 1px solid #4E342E;
}
.carrito__boton-confirmar:hover {
  background-color: #F5E6D3;
  border: 1px solid #F5E6D3;
  color: #4E342E;
}