@charset "UTF-8";
.inicio {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.inicio__titulo {
  font-size: 60px;
}
.inicio__subtitulo {
  font-weight: 400;
  margin-bottom: 40px;
}
.inicio__registro {
  color: #4E342E;
  text-decoration: underline;
  margin-top: 10px;
}
.inicio__registro:hover {
  color: #D4AF37;
}
.inicio__ventajas {
  display: flex;
  justify-content: space-between;
  max-width: 450px;
  width: 100%;
  margin-top: 30px;
}
.inicio__elemento {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.inicio__elemento p {
  margin-top: 0;
}

.tienda {
  margin-top: 90px;
  display: flex;
  justify-content: space-between;
}
.tienda h2 {
  margin-bottom: 40px;
}
.tienda p {
  font-weight: 600;
}
.tienda img {
  display: block;
}
.tienda ul {
  width: 100%;
}
.tienda .filtro {
  display: flex;
  justify-content: space-between;
  margin: 0;
}
.tienda .filtro__input {
  height: 24px;
  border: 1px solid #4E342E;
  color: #4E342E;
}
.tienda .filtro__select {
  border: 1px solid #4E342E;
  height: 25px;
  color: #4E342E;
  margin-right: 0;
}
.tienda .filtro__select-texto {
  font-size: 20px;
}
.tienda .productos {
  display: flex;
  flex-direction: column;
  margin-right: 20px;
}
.tienda .productos__contenedor {
  display: grid;
  column-gap: 25px;
}
.tienda .productos__imagen {
  width: 100%;
  border-radius: 10px;
}
.tienda .productos__imagen--seleccionado {
  outline: 3px solid #4E342E;
}
.tienda .productos__categoria {
  font-weight: 400;
  margin-bottom: 10px;
}
.tienda .productos__nombre {
  margin: 0;
}
.tienda .productos__precio {
  margin-top: 10px;
  margin-bottom: 30px;
}
.tienda .productos__cantidad {
  padding-right: 7.5px;
  padding-left: 7.5px;
  color: hsl(14, 86%, 42%);
}
.tienda .productos__boton-añadir {
  margin-top: -22.5px;
  width: 64%;
}
.tienda .productos__boton-cantidad {
  margin-top: -22.5px;
  background-color: #4E342E;
  outline: 1px solid #4E342E;
  width: 75%;
}
.tienda .productos__boton-sumar, .tienda .productos .productos__boton-restar {
  background-color: #4E342E;
  color: #F5E6D3;
  height: 100%;
  width: 100%;
  display: grid;
  place-content: center;
}
.tienda .productos__boton-restar {
  border-top-left-radius: 50px;
  border-bottom-left-radius: 50px;
}
.tienda .productos__boton-sumar {
  border-top-right-radius: 50px;
  border-bottom-right-radius: 50px;
}
.tienda .productos__boton-sumar:hover, .tienda .productos .productos__boton-restar:hover {
  background-color: #F5E6D3;
  color: #4E342E;
  cursor: pointer;
}
.tienda .productos__boton-sumar:hover img, .tienda .productos .productos__boton-restar:hover img {
  filter: invert(0.4) sepia() hue-rotate(-16deg) saturate(86) brightness(42);
}
.tienda .productos .alerta {
  width: 100%;
}
@media (min-width: 545px) {
  .tienda .productos__contenedor {
    grid-template-columns: repeat(2, 250px);
  }
  .tienda .resumen-orden {
    width: 41%;
  }
}
@media (min-width: 820px) {
  .tienda .productos__contenedor {
    grid-template-columns: repeat(3, 250px);
  }
}
@media (min-width: 1201px) {
  .tienda .contenedor {
    display: flex;
  }
}
.tienda .boton {
  height: 45px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  align-self: center;
  padding: 0;
}
.tienda .boton input {
  width: 100%;
  text-align: center;
  background-color: #4E342E;
  border: none;
  height: 100%;
  color: #F5E6D3;
}
.tienda input[type=number]::-webkit-inner-spin-button,
.tienda input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.tienda input[type=number] {
  -moz-appearance: textfield;
}

.checkout {
  display: flex;
  justify-content: space-between;
}
.checkout .formulario {
  width: 60%;
}
@media (max-width: 768px) {
  .checkout .formulario {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 385px;
  }
}
.checkout .contenedor__metodo-entrega .formulario__campo {
  margin-bottom: 0;
}
.checkout .campo-direccionElegida {
  display: flex;
  gap: 10px;
  margin-left: 20px;
}
@media (max-width: 768px) {
  .checkout .campo-direccionElegida {
    margin-left: 0;
  }
}
.checkout #result-message {
  width: 385px;
}
@media (max-width: 768px) {
  .checkout {
    flex-direction: column;
    align-items: center;
  }
  .checkout .boton--nueva-direccion {
    margin-right: auto;
    margin-left: auto;
  }
}
@media (max-width: 480px) {
  .checkout {
    padding: 0 0 0 0;
  }
}